<template>
    <!-- <div class="chose-panel" v-show="popShow" @click="popShow=false" @touchmove.prevent> -->
        <div class="chose-panel" v-show="popShow" @touchmove.prevent>
        <div class="backgImg" v-show="phone_box">
            <div class="banner-pop-up" @click.stop="userClick=true">
               <div class="btn-pop-up">
                    <div class="btn-input">
                        <input type="tel" v-model="phone" placeholder="" @keyup="telText">
                    </div>
                    <div class="btn-free btnRed" >
                         <button class="haveNot " :class="{btnRed:showBtn}" :disabled="showBtn===false" @click.stop="not_phone_rq()"></button>
                    </div>
               </div>
            </div>
        </div>
   </div>
</template>
<script>
export default {
    data(){
        return{
            //
            popShow: true,
            /**-:false */
            phone_box: false,
            //
            phone: '',
            //button
            showBtn: false,
            /**data */
            power_data: {
                 /**-- */
                author_url: null,
                /**-id */
                not_phone_id: null,
                
            },

        }
    },
    mounted: function(){
        /**url */
        var this_url = window.location.href;
        /**
         * 1-,code 
         * 2
         * **/
        // alert('token:::'+window.sessionStorage.getItem("token"));
        alert(this_url);
        alert('token:::'+window.sessionStorage.getItem("token"));
        // if(this_url.indexOf('code=') != -1 && !window.sessionStorage.getItem("token")){
        if(this_url.indexOf('code=') != -1){
            /**urlstring */
            // alert(this_url);
            /** codestart */
            var ginseng_str = this_url.split('code=')[1];
             /**code */
            var code_val = ginseng_str.split('&state=')[0];
            /**  token*/
            var token_val = '';
            if(this_url.split('token=')[1]){
                token_val = this_url.split('token=')[1];
            }
           
            alert('code:'+code_val);
            /* axios=> -s*/
            this.$axios.post("/user/get_code",{
                code: code_val
                })
                .then((_res)=>{
                    console.log('-:',_res['data']['data']);
                   
                    // alert('-1:'+_res['data']);
                    alert('-2:'+_res['data']['data']);
                    if(+_res['data']['status'] == -2){
                        alert('')
                    }
                    /**- */
                    if(_res['data']['data']['is_checked'] == 1){
                        alert('-'+ _res['data']['data']['token'])
                        /**-- */
                        // this.phone_box = false;
                        /**
                         *  
                         * token;
                         * **/
                        window.sessionStorage.setItem("token", _res['data']['data']['token']);    
                        /**-  */
                        this.popShow = false;
                        return false;
                    }
                     /**- */
                    if(_res['data']['data']['is_checked'] == 0){
                        alert('-'+_res['data']['data']['id'])
                        /**-  */
                        this.phone_box = true;
                        /** 
                         * -id => 
                         * **/
                        this.power_data['not_phone_id'] =  _res['data']['data']['id'];
                        return false;
                    }
                    
                })
                .catch((_error) => {
                    alert('-'+_error);
                    console.log(_error);
                })
            /* axios=> -e*/


        } else {
            /*axios=> -s*/
            this.$axios.post("user/login")
                .then((res)=>{
                    console.log('-:',res['data']['data']);
                    alert('-:'+ res['data']['data']);
                    /**- */
                    if(res['data']['status']){
                        /**-- */
                        this.power_data['author_url'] = res['data']['data'];
                        this.isWeiXin(); 
                    }
                })
                .catch((error) => {
                    alert('-'+error);
                    console.log(error);
                })
            /*axios=> -e*/
        }
        
    },
    methods:{
        /**- */
        isWeiXin() {
            /**1 => 1 2 =>  */
            // var type_start = null;
            var ua = window.navigator.userAgent.toLowerCase();
            /**=>  &&  */
            if (ua.match(/MicroMessenger/i) == 'micromessenger' && /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
                // alert('');
                window.location.href = this.power_data['author_url'];
                // type_start = 1;
            }else {
                // alert('-22');
                // type_start = 2;
                /**-  */
                this.phone_box = true;
            }
        },
        //  button
        telText(){
             var mobile=/^[1]([3-9])[0-9]{9}$/;
             if(this.phone!='' && mobile.test(this.phone)){  //
                 this.showBtn=true
             }else{
                 this.showBtn=false
             }
        },
        /** */
        not_phone_rq() {
            
            /*=> axios=> -s*/
            this.$axios.post("user/binding_mob",{
                id: this.power_data['not_phone_id'],
                mobile: this.phone,
            })
                .then((_data) => {
                    alert('-1:'+_data['data']['token']);
                    alert('-2:'+_data['data']['data']['token']);
                     
                     /**
                     *  
                     * token;
                     * **/
                    window.sessionStorage.setItem("token", _res['data']['data']['token']);    
                })
                .catch((_err) => {
                    alert('-'+_err);
                    console.log(_err);
                })
            /*=> axios=> -e*/
        }

    }
}
</script>
<style lang="stylus" scoped>
    .chose-panel 
        background: rgba(0,0,0,.3);
        bottom: 0;
        height: 100%;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        z-index: 999;
    .backgImg
        width 100%
        height 70%
        // max-height 100%
        margin-top  237px
        margin-bottom 25px
        position relative
    .banner-pop-up
        background url('/static/img/home/new_red_package.png') no-repeat
        background-size 100% 100%
        height 100%
        width 100%
    
    .btn-pop-up
        position absolute
        left 0
        bottom 120px
        width 100%
    
    .btn-input
         width 480px
         height 120px
         background #ffffff
         border-radius 60px
         margin 0 auto 23px
    
    .btn-input>input
         border 0
         width 480px
         height 120px
         background #ffffff
         border-radius 60px
         text-align center
         font-size 36px

    .btn-free
         width 480px
         height 120px
         background-color: #b9b9b9
         border-radius 60px
         margin 0 auto
    
    .btn-free .haveNot
         border none
         text-align center
         line-height 120px
         width 480px
         border-radius 60px
         color #ffffff
         font-size 36px
    
    .btn-free .btnRed
         background-color #ff1233
input::-webkit-input-placeholder 
        color: #bdbdbd;

   
</style>




