import Axios from 'axios'
import router from '@/router'
import store from '@/vuex/vueShop'
//  || 
var root = process.env.API_ROOT;
const axios = Axios.create();
import { Dialog } from 'vant';
let cancel ,promiseArr = {} 
/* axios*/
axios.interceptors.request.use(
	config => {
		//url
		config.url = root + config.url;
		// config.baseURL = '/api' // axios => 
		config.withCredentials = true // token ,
		config.timeout = 1000 // 
		// let token = localStorage.getItem('Authorization');
		// if(token) {
		// 	config.headers = {
		// 		'Authorization': token,
		// 		'Content-Type': 'application/x-www-form-urlencoded'
		// 	}
		// }
		 //
		if (promiseArr[config.url]) {
			promiseArr[config.url]('')
			promiseArr[config.url] = cancel
		} else {
			promiseArr[config.url] = cancel
		}
		return config
	},
	error => {
		console.log(error)
		return Promise.reject(error)
	})

// http response  //
axios.interceptors.response.use(
	response => {
		if(response.data.code === 10000) {
			console.log('-');
			// Dialog.alert({
			// 	message: ''
			// }).then(() => {
			// 	store.commit('del_token'); //token
			// 	router.replace({ //
			// 		path: '/login',
			// 		query: {
			// 			redirect: router.currentRoute.fullPath
			// 		}
			// 	})
			// })
		}
		return response;
	},
	error => {
		if(error && error.response) {
			switch(error.response.code) {
				case 400:
					error.message = ''
					break;
				case 401:
					Dialog.alert({
						message: ''
					}).then(() => {
						// store.commit('del_token'); //token
						// router.replace({ //
						// 	path: '/login',
						// 	query: {
						// 		redirect: router.currentRoute.fullPath
						// 	}
						// })
					})
				   break;
				case 403:
					error.message = ''
					break;
				case 404:
					error.message = ''
					break;
				case 405:
					error.message = ''
					break;
				case 408:
					error.message = ''
					break;
				case 500:
					error.message = ''
					// Dialog.alert({
					// 	message: ''
					// }).then(() => {
					// 	$router.go(-1)
					// })
					break;
				case 501:
					error.message = ''
					break;
				case 502:
					error.message = ''
					break;
				case 503:
					error.message = ''
					break;
				case 504:
					error.message = ''
					break;
				case 505:
					error.message = 'http'
					break;
				default:
					error.message = `${error.response.status}`
				
			}
		}else{
			error.message = ''
			console.log(error.message)
		}
		console.log(Promise.reject(error.response.data))
		return Promise.reject(error.response.data)
	});

export default axios;


