                                </div>
                            </div>
                            <div class="total-bar">
                                <div class="total-count">2 </div>
                                <div class="payment">
                                    <span class="label"> : </span>
                                    <span class="price">736.00</span>
                                </div>
                            </div>
                            <div class="order-btn">
                                <span class="btn"></span>
                                <span class="btn red"></span>
                            </div>
                        </div>
                        <div class="item-card">
                            <div class="card-head">
