                                <span class="order-state"></span>
                            </div>
                            <div class="goods-item">
                                <div class="img-wrap">
                                    <img src="/static/img/order/00order-goods-img01.png" />
                                </div>
                                <div class="text">
                                    <h3>Haoduoyi2018 </h3>
                                </div>
                            </div>
                            <div class="total-bar">
                                <div class="total-count">2 </div>
                                <div class="payment">
                                    <span class="label"> : </span>
                                    <span class="price">736.00</span>
                                </div>
                            </div>
                            <div class="order-btn">
                                <span class="btn red"></span>
                            </div>
                        </div>
                        <div class="item-card">
                            <div class="card-head">
                                <span class="order-date">2019-02-03 00:28:20</span>
                                <span class="order-state"></span>
                            </div>
                            <div class="goods-item">
                                <div class="img-wrap">
                                    <img src="/static/img/order/00order-goods-img01.png" />
                                </div>
                                <div class="text">
                                    <h3>Haoduoyi2018 </h3>
                                </div>
                            </div>
                            <div class="total-bar">
                                <div class="total-count">2 </div>
                                <div class="payment">
                                    <span class="label"> : </span>
                                    <span class="price">736.00</span>
                                </div>
                            </div>
                            <div class="order-btn">
                                <span class="btn red"></span>
                                <span class="btn red"></span>
                            </div>
                        </div>
                    </li>
                    <li>
                        
                    </li>
                </ul>
            </div>

        </div>

    </div>
</template>

<script>
import headtop from '@/components/public/page_top_head'
export default {
    name:'order',
    components: {
        headtop
    },
    data(){
        return{
            nowIndex:0,
            type:this.$route.query.type,
            // tab
            tabList:[
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                },
                {
                    tabTitle:""
                }
            ]
        }
    },
    /*DOM*/
    created: function(){
        /**head=>style */
        var style_obj = {
            head_wrap_style: {
                background: '#ffffff',
            },
            return_data: {
                icon: '/static/img/public/left_icon_555.png',
                /*(),=>*/
                url_data: '',
            },
            title: {
                /**- */
                need_model: true,
                name: '',
                style: {color:'#151515'}
            },
            right_button: {
                need_model: false,
                /**=>=>  */
                text: '',
                /**-text */
			    change_text: '',
                /**=>(styleclass) */
                icon: {
                    class: '',
                    icon_url: '',
                },
                /**- */
                show: false,
                url_data: '',
            },
        }
        /**vuex */
        this.$store.commit('change_head',style_obj);

    },
    methods:{
        // tab
        handleClick(index){
            this.nowIndex = index
            this.$router.push('/order?type='+index)
            this.type = this.$route.query.type
            this.dataRendering()
        },
        // 
        dataRendering(){
            console.log(1)
        }
    }

}
</script>

<style lang="stylus" scoped>
.order
    .tab-wrap
        padding-top 88px
        .tab-tit
            border-top 2px solid #e6e6e6
            ul
                height 70px
                line-height 70px
                font-size 30px
                background-color #ffffff
                display flex
                align-items center
                justify-content space-between
                padding 0 24px
                box-sizing border-box
                margin-bottom 20px
                li
                    color #151515
                    border-bottom 4px solid transparent
                    &.active
                        border-color #f11010
                        color #f11010
        .tab-con
            padding 0 24px
            box-sizing border-box
            .item-card
                background-color #ffffff
                border-radius 8px
                box-shadow 0 0 8px #e6e6e6
                margin-bottom 14px
                .card-head
                    height 55px
                    border-bottom 2px solid #e6e6e6
                    display flex
                    align-items center
                    justify-content space-between
                    padding 0 36px
                    box-sizing border-box
                    .order-date
                        font-size 22px
                        color #151515
                    .order-state
                        font-size 24px
                        color #f20c0c
                .goods-item
                    display flex
                    padding 17px 38px 10px
                    box-sizing border-box
                    .img-wrap
                        width 160px
                        height 160px
                        text-align center
                        margin-right 44px
                        img 
                            max-width 100%
                    .text
                        flex 1
                        h3
                            font-size 24px
                            line-height 34px
                            font-weight normal
                            overflow hidden
                            text-overflow ellipsis
                            display -webkit-box
                            -webkit-line-clamp 2
                            -webkit-box-orient vertical
                .total-bar
                    height 40px
                    color #6f6f6f
                    display flex
                    justify-content flex-end
                    align-items center
                    margin-bottom 10px  
                    padding 0 38px
                    box-sizing border-box
                    .total-count
                        line-height 40px
                    .payment
                        margin-left 20px
                        font-size 30px
                        line-height 40px
                        .label
                            font-size 22px
                            position relative
                            top -2px
                        .price
                            color #151515
                            font-size 30px
                .order-btn
                    display flex
                    justify-content flex-end
                    padding 0 38px 36px
                    box-sizing border-box
                    .btn
                        width 130px
                        height 40px
                        line-height 40px
                        color #888888
                        text-align center
                        display inline-block
                        font-size 26px
                        border-radius 15px
                        border 2px solid #888888
                        margin-left 26px
                        &.red
                            color #f20c0c
                            border-color #f20c0c                    


                

</style>



